.todo-input-wrapper {
  position: relative;
  border-bottom: 1px solid #ededed;
}

.todo-input {
  padding: 16px 16px 16px 60px;
  border: none;
  background: rgba(0, 0, 0, 0.003);
  width: 100%;
  box-sizing: border-box;
  font-size: 28px;
  box-shadow: inset 0 -2px 40px rgba(0,0,0,0.03);
}

.fa-plus {
  position: absolute;
  font-size: 24px;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
}
